.body{
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 100vh;
      background: #060c21;
      font-family:'Poppins', sans-serif;
  }
  .container{
      position: relative;
      width: 90%;
      display: grid;
      grid-template-columns: repeat(auto-fill,minmax(346px, 1fr));
      grid-template-rows: auto;
      grid-gap: 0 40px;
  }
  
  .container .box{
      position: relative;
      display: flex;
      border: 1px solid #000;
      height: 400px;
      background: #060c21;
      align-items: center;
  }
  
  .container .box:before{
      content: '';
      position: absolute;
      top: -2px;
      left: -2px;
      right: -2px;
      bottom: -2px;
      background: #fff;
      z-index: -1;
      transform: skew(2deg,2deg);
  }
  
  .container .box:nth-child(1):before{
      background: linear-gradient(315deg, #ff0057,#e64a19);
  }
  .container .box:nth-child(2):before{
      background: linear-gradient(315deg, #89ff00,#00bcd4);
  }
  .container .box:nth-child(3):before{
      background: linear-gradient(315deg, #e91e63,#5d02ff);
  }
  .container .box:nth-child(4):before{
      background: linear-gradient(315deg, #ff0000,#ffc107);
  }
  
  .container .box:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 50%;
      height: 100%;
      background: rgba(255, 255, 255, 0.05);
      pointer-events: none;
  }
  
  .content{
      position: relative;
      padding: 20px;
      transform: translateY(40px);
  }
  
  .content .h2{
      position: absolute;
      top: -60px;
      right: 20px;
      margin: 0;
      padding: 0;
      font-size: 10em;
      color: rgba(255, 255, 255, .05);
      transition: 0.5s;
  }
  
  .box:hover .content .h2{
      top: -140px;
  }
  
  .box .content .h3{
      margin: 0 0 10px;
      padding: 0;
      font-size: 24px;
      font-weight: 500;
      color: #fff;
  }
  
  .box .content .p{
      margin: 0;
      padding: 0;
      color: #fff;
      font-size: 16px;
  }
  
  .box .content a{
      position: relative;
      margin: 5px 0 0 ;
      padding: 10px 20px ;
      text-decoration: none;
      border: 1px solid #fff;
      display: inline-block;
      transition: 0.5s;
      transform: translateY(-40px);
      visibility: hidden;
      opacity: 0;
      color: #fff;
  }
  
  .box:hover .content a{
      transform: translateY(0);
      visibility: visible;
      opacity: 1;
  }
  
  .box .content a:hover{
      color: #000;
      background: #fff;
  }